.navbar {
    margin: 0;
    padding: 0;
    background-color: #1F2833;
}

.navbar-container{
    display: flex;
    flex-direction: row;
    height: 50px;
    font-family: 'Ubuntu', sans-serif;
}

#navLink{
    margin-left: 20px;
    align-self: center;
}

a:link {
    color:  #a9a9a9;
    text-decoration: none;
  }

  a:visited {
    color:  #a9a9a9;
    text-decoration: none;
  }
  
  /* mouse over link */
  a:hover {
    color: white;
    text-decoration: none;
  }
  
  /* selected link */
  a:active {
    color: white;
    text-decoration: none;
  }